	module.exports = {"data":{"challengeNode":{"title":"P2P Video Chat Application","description":["<strong>目标：</strong> Build a <a href='https://glitch.com' target='_blank'>Glitch</a> app that is functionally similar to this: <a href='https://grove-voice.glitch.me/' target='_blank'>https://grove-voice.glitch.me</a>.","满足下列<a href='https://en.wikipedia.org/wiki/User_story' target='_blank'>需求</a>。你可以使用任何你需要的库或 API。发挥你自己的代码风格。","<strong>需求：</strong>启动应用后，浏览器会提示我给予其权限使其可以访问网络摄像头和麦克风。 ","<strong>需求：</strong>在我授权后，系统会提示我输入房间名称。","<strong>需求：</strong>如果该名称没有被占用，系统将会自动生成一个房间。","<strong>需求：</strong>我的好友也可以访问该网站，进入到我所在的房间，和我进行视频聊天。","<strong>需求：</strong>如果我输入一个已经有两个人的房间的名字，系统会提示我该房间已满员。","<strong>需求：</strong>任何人都可以创建或加入任何房间。可以有许多房间，但所有房间的名字必须是唯一的。","<strong>需求：</strong>我可以选择不允许网站访问我的麦克风和网络摄像头。如果我选择这样做，当发生驱动问题时，系统会提示:访问麦克风和网络摄像头是必需的。","<strong>需求：</strong>当我选择跳过输入房间名称这一步骤，或者我没有输入名字，或者只输入空格时，系统会要求我输入有效的房间名称。","<strong>需求：</strong>如果房间里的两个人中有一个断开了连接，他们可以重新连接到同一个房间并继续聊天。","如果你遇到了问题，请点击 <a href='https://forum.freecodecamp.one/t/topic/157' target='_blank'>帮助</a>。","When you are finished, click the \"我已经完成了这个挑战\" button and include a link to your Glitch App.","你可以通过将你的项目分享到社交网络来获得反馈。"],"challengeType":3,"fields":{"blockName":"Take Home Projects","slug":"/coding-interview-prep/take-home-projects/p2p-video-chat-application"}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/take-home-projects/show-national-contiguity-with-a-force-directed-graph","id":"bd7150d8c442eddfafb5bd1c"},"slug":"/coding-interview-prep/take-home-projects/p2p-video-chat-application"}}

